// Journal Entries API Route
// GET/POST /api/v1/accounting/journal-entries

import { NextRequest, NextResponse } from 'next/server'
import { internalAccountingService } from '@/lib/accounting'

export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const status = searchParams.get('status')
    const referenceType = searchParams.get('referenceType')
    const startDate = searchParams.get('startDate')
    const endDate = searchParams.get('endDate')
    const page = parseInt(searchParams.get('page') || '1')
    const limit = parseInt(searchParams.get('limit') || '20')

    if (!subAccountId) {
      return NextResponse.json(
        { error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    let entries = internalAccountingService.getJournalEntries(subAccountId, {
      status: status || undefined,
      referenceType: referenceType || undefined,
      startDate: startDate || undefined,
      endDate: endDate || undefined,
    })

    // Calculate pagination
    const total = entries.length
    const start = (page - 1) * limit
    const end = start + limit
    entries = entries.slice(start, end)

    // Calculate totals
    const allEntries = internalAccountingService.getJournalEntries(subAccountId)
    const stats = {
      totalEntries: allEntries.length,
      posted: allEntries.filter(e => e.status === 'posted').length,
      draft: allEntries.filter(e => e.status === 'draft').length,
      voided: allEntries.filter(e => e.status === 'voided').length,
      totalDebit: allEntries.filter(e => e.status === 'posted').reduce((sum, e) => sum + e.totalDebit, 0),
      totalCredit: allEntries.filter(e => e.status === 'posted').reduce((sum, e) => sum + e.totalCredit, 0),
    }

    return NextResponse.json({
      success: true,
      data: {
        entries,
        pagination: {
          page,
          limit,
          total,
          totalPages: Math.ceil(total / limit),
        },
        stats,
      },
    })
  } catch (error: any) {
    console.error('Get journal entries error:', error)
    return NextResponse.json(
      { error: error.message || 'Failed to get journal entries' },
      { status: 500 }
    )
  }
}

export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      subAccountId,
      date,
      description,
      reference,
      referenceType,
      referenceId,
      lines,
      autoPost,
    } = body

    if (!subAccountId || !date || !description || !lines || lines.length < 2) {
      return NextResponse.json(
        { error: 'Missing required fields: subAccountId, date, description, lines (min 2)' },
        { status: 400 }
      )
    }

    // Validate lines
    const totalDebit = lines.reduce((sum: number, l: any) => sum + (l.debit || 0), 0)
    const totalCredit = lines.reduce((sum: number, l: any) => sum + (l.credit || 0), 0)

    if (totalDebit !== totalCredit) {
      return NextResponse.json(
        { error: `Journal entry must balance. Debit: ${totalDebit}, Credit: ${totalCredit}` },
        { status: 400 }
      )
    }

    if (totalDebit === 0) {
      return NextResponse.json(
        { error: 'Journal entry cannot have zero total' },
        { status: 400 }
      )
    }

    // Get account details for each line
    const processedLines = lines.map((line: any, idx: number) => {
      const account = internalAccountingService.getGLAccountByCode(
        subAccountId,
        line.accountCode
      )

      if (!account) {
        throw new Error(`Account not found: ${line.accountCode}`)
      }

      return {
        id: `jel_${Date.now()}_${idx}`,
        accountId: account.id,
        accountCode: account.accountCode,
        accountName: account.accountName,
        description: line.description,
        debit: line.debit || 0,
        credit: line.credit || 0,
        taxCodeId: line.taxCodeId,
        taxAmount: line.taxAmount,
      }
    })

    const entry = internalAccountingService.createJournalEntry(subAccountId, {
      date,
      description,
      reference,
      referenceType,
      referenceId,
      lines: processedLines,
      status: autoPost ? 'posted' : 'draft',
      totalDebit,
      totalCredit,
    })

    return NextResponse.json({
      success: true,
      data: { entry },
    }, { status: 201 })
  } catch (error: any) {
    console.error('Create journal entry error:', error)
    return NextResponse.json(
      { error: error.message || 'Failed to create journal entry' },
      { status: 500 }
    )
  }
}
